}
.topPadding{
    padding-top: 55px;
}

.bg-dark {
    background: black!important;
}

.pageTitle{
    text-align: center;
    padding: 5px;
}
/* Header */
.topBarNav{
    width: 50px;
}
.nav-link:focus {
    color: white!important;
}

/* footer */
.footerLinks
{
    padding-top: 70px;
}

.footerLinks .centre {
    justify-content: center;
    font-size: 12px;
    text-align: center;
}

.footerLinks .iconRightSpacing{
    padding-right: 20px!important;
    padding-left: 15px!important;
}



/* Home */
.carousel-indicators li{
    width: 10px!important;
    height: 2px!important;
}


.homePageDescription .description{
    text-align: center;
    color: white;
    padding: 20px;
    font-family: inherit;


}

.homepageServices {
    text-align: center;
}

.homepageServices .servicesTitle{
    color: white;
    font-family: inherit;
}

.abcd{
    overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
}

.abcd::-webkit-scrollbar { width: 0 !important }

.cardStyle
{
    display: inline-block;
    width: 256px;
    margin: 10px;
    white-space: pre-wrap;
    text-align: left;
    background: #dedede!important;

}

.homepageContactInfo{
    color: white;
    text-align: center;
    font-family: inherit;
}


.test{
    display: 'flex';
    flex-wrap: 'wrap';
    justify-content: 'space-around';
    overflow: 'hidden';
}

.testdimension{
    width: 500;
    height: 450;
}

